<template>
    <div>
        <div>
            <v-data-table
                :headers="headers"
                :items="accessoryItems"
                item-key="id"
                :loading="loading"
                disable-sort
                hide-default-footer
            >
                <!-- eslint-disable  -->

                <template v-slot:item.direction="{item}">
                    <div class="d-flex">
                        <span class="text-black">{{ item.from_where }}</span>
                    </div>
                </template>

                <template v-slot:item.type="{item}">
                    <div class="d-flex justify-center">
                        <span class="text-black">{{ item.product.product_category.name }}</span>
                    </div>
                </template>

                <template v-slot:item.name="{item}">
                    <div class="d-flex justify-center">
                        <span class="text-black">{{ item.product.name }}</span>
                    </div>
                </template>

                <template v-slot:item.code="{item}">
                    <div class="d-flex justify-center">
                        <span class="text-black">#{{ item.product.code }}</span>
                    </div>
                </template>

                <template v-slot:item.amount="{item}">
                    <div class="d-flex justify-center">
                        <span class="text-black">{{ formatPrice(item.amount) }} kg</span>
                    </div>
                </template>

                <template v-slot:item.price="{item}">
                    <div class="d-flex justify-center">
                        <span class="text-black">{{ formatPrice(item.price) }} so`m</span>
                    </div>
                </template>

                <template v-slot:item.sum="{item}">
                    <div class="d-flex justify-center">
                        <span class="text-black">{{ formatPrice(item.total_price) }} so`m</span>
                    </div>
                </template>

                <template v-slot:item.date="{item}">
                    <div class="d-flex justify-center">
                        <span class="text-black">{{ $dayjs(item.created_at).format('DD-MM-YYYY') }}</span>
                    </div>
                </template>

                <template v-slot:no-data>
                    <div>
                        <no-data class="mt-6"></no-data>
                        <br>
                        <h2>Ma'lumotlar topilmadi!</h2>
                    </div>
                </template>

                <template v-slot:item.actions="{item}">
                    <div class="d-flex align-center justify-center">
                        <div class="cursor-pointer mr-2 mt-1"
                        @click="(product = item) && (editModal = true )"
                        v-if="$can('update', 'supply_aksessuar_kirim')">
                            <img src="@/assets/icons/edit.svg" alt="edit" class="cursor-pointer">
                        </div>
                        <div class="cursor-pointer mt-1" @click="(element = item) && (deleteModal = true )"
                        v-if="$can('delete', 'supply_aksessuar_kirim')">
                            <img src="@/assets/icons/trush-square.svg" alt="edit-2">
                        </div>
                    </div>
                </template>

                <template v-slot:no-data>
                    <div>
                        <no-data class="mt-6"></no-data>
                        <br>
                        <h2 class="ml-6 mt-1">Ma'lumotlar topilmadi!</h2>
                    </div>
                </template>

            </v-data-table>

            <v-pagination
                v-model="page"
                :length="pageCount"
                total-visible="7"
                class="my-2"
            ></v-pagination>

        </div>






        <v-dialog
            max-width="400"
            persistent
            v-model="editModal"
        >
            <v-card class="aksessuar_add">

                <div class="aksessuar_add__title">
                    <span class="text-black">Mahsulot tahriri</span>
                    <v-btn icon @click="closeEditDialog">
                        <img src="@/assets/images/BackButton.svg" alt="BackButton">
                    </v-btn>
                </div>


                <ValidationObserver ref="formEditIncomeAcessory" v-slot="{ handleSubmit, invalid }">
                    <form class="aksessuar_add__content" @submit.prevent="handleSubmit(onSubmit)">

                        <div class="mt-5">
                            <span class="text-black ml-1">Partiya</span>
                            <ValidationProvider :rules="{required: true,
                             error: [errorStatus, errorMessage, errorPlace, 'from_where']}"
                              v-slot="{ errors }">
                                <v-text-field outlined v-model="product.from_where" class="mt-2"
                                    placeholder="Misol: Buxorodan" :error-messages="errors">
                                </v-text-field>
                            </ValidationProvider>
                        </div>


                        <div class="mt-5">
                            <span class="text-black ml-1">Massa</span>
                            <ValidationProvider :rules="{required: true,
                             error: [errorStatus, errorMessage, errorPlace, 'amount']}"
                              v-slot="{ errors }">
                                <v-text-field outlined :value="formatPrice(product.amount)" class="mt-2"
                                    @input="formatPrice2($event, 'amount')"
                                    placeholder="Misol: 200 kg" :error-messages="errors">
                                </v-text-field>
                            </ValidationProvider>
                        </div>


                        <div class="mt-5">
                            <span class="text-black ml-1">KG narxi</span>
                            <ValidationProvider :rules="{required: true,
                             error: [errorStatus, errorMessage, errorPlace, 'price']}"
                              v-slot="{ errors }">
                                <v-text-field outlined :value="formatPrice(product.price)" class="mt-2"
                                    @input="formatPrice2($event, 'price')"
                                    placeholder="Misol: 1,000 so`m" :error-messages="errors">
                                </v-text-field>
                            </ValidationProvider>
                        </div>


                        <div class="mt-5">
                            <span class="text-black ml-1">Umumiy summa</span>
                            <ValidationProvider :rules="{required: true,
                             error: [errorStatus, errorMessage, errorPlace, 'total_price']}"
                              v-slot="{ errors }">
                                <v-text-field outlined :value="formatPrice(product.total_price)" class="mt-2"
                                    @input="formatPrice2($event, 'total_price')"
                                    placeholder="Misol: 100,000 so`m" :error-messages="errors">
                                </v-text-field>
                            </ValidationProvider>
                        </div>

                        <v-card-actions class="d-flex justify-space-between">
                            <v-btn
                                text
                                class="backBtn"
                                elevation="0"
                                @click="closeEditDialog"
                            >Bekor qilish
                            </v-btn>
                            <v-btn
                                class="primaryBtn"
                                elevation="0"
                                :loading="loading"
                                :disabled="invalid"
                                type="submit"
                                @click="loader = 'loading'"
                            >Tahrirlash
                            </v-btn>
                        </v-card-actions>

                    </form>
                </ValidationObserver>
            </v-card>
        </v-dialog>



        <DeleteModal
        :deleteModal="deleteModal"
        :user_id="{id: element.id, type: 'accessory'}"
        :actionName="'warehouse/deleteProductIncome'"
        :modalTitle="'Aksessuar o‘chirilishi'"
        :modalDesc="'Bu aksessuarni o‘chirmoqchimisiz?'"></DeleteModal>
    </div>
</template>

<script>
import {mapActions, mapState} from "vuex";
import { loaderMixin } from '@/mixins/dateLoaderMixins.js'
import { priceMixin } from '@/mixins/formatter.js';
import NoData from "../../../components/base/NoData";
import DeleteModal from '@/components/modals/DeleteModal.vue'

export default {
    mixins: [loaderMixin, priceMixin],
    components: {NoData, DeleteModal},
    data() {
        return {
            // ============= SECONDARY_DATA =============
            page: 1,
            pageCount: 0,
            accessoryItems: [],
            editModal: false,
            deleteModal: false,
            headers: [
                {text: 'Qayerdan', value: 'direction', align: 'start', class: 'table-header'},
                {text: 'Turi', value: 'type', align: 'center', class: 'table-header'},
                {text: 'Nomi', value: 'name', align: 'center', class: 'table-header'},
                {text: 'Kodi', value: 'code', align: 'center', class: 'table-header'},
                {text: 'Adadi', value: 'amount', align: 'center', class: 'table-header'},
                {text: 'KG narxi', value: 'price', align: 'center', class: 'table-header'},
                {text: 'Summa', value: 'sum', align: 'center', class: 'table-header'},
                {text: 'Sana', value: 'date', align: 'center', class: 'table-header'},
                {text: 'Harakatlar', value: 'actions', align: 'center', class: 'table-header'},
            ],
            element: {},
            product: {
                from_where: '',
                amount: '',
                price: '',
                total_price: '',
            },
            // ============= SECONDARY_DATA =============



            // ============= PRIMARY_DATA =============
            errorStatus: false,
            errorMessage: '',
            errorPlace: ''
            // ============= PRIMARY_DATA =============
        }
    },
    watch: {
        page: async function (newVal, oldVal) {
            if (newVal) {
                await this.refresh()
            }
        },
        '$store.state.main.supply_accessory_income': {
            handler: async function(value) {
                if(value){
                    await this.refresh()
                    this.$store.commit('main/SET_UPDATE_SUPPLY_ACCESSORY_INCOME', false)
                }
            },
            deep: true,
            immediate: true
        },
    },
    async mounted() {
        await this.refresh();

        if(!this.$can('update', 'supply_aksessuar_kirim') && !this.$can('delete', 'supply_aksessuar_kirim')){
            this.headers.pop();
        }
    },
    computed: {
        ...mapState({
            supply: state => state.supply.accessory,
            searchValue: state => state.main.searchValue,
                dateFilterValue: state => state.main.dateFilterValue
        })
    },
    methods: {
        ...mapActions({
            fetchAccessory: 'supply/fetchAccessory',
            updateProductIncome: 'warehouse/updateProductIncome'
        }),

        async refresh() {
            this.loading = true;
            await this.fetchAccessory({page: this.page, status: 4, search: this.searchValue, date: this.dateFilterValue})
            this.page = this.supply.current_page;
            this.pageCount = Math.ceil(this.supply.total / 10);
            this.accessoryItems = this.supply.data;
            this.loading = false;
        },










        // ============= PRICE_FORMATTER START =============
        formatPrice2(value, input) {
            if(value != ''){
                let currency = value.toString().replace(/,/g, '');
                currency = parseFloat(currency)
                currency = new Intl.NumberFormat('en-US', {minimumFractionDigits: 0, maximumFractionDigits: 2}).format(currency);


                if(input == 'amount'){
                    this.product.amount = currency
                }else if(input == 'price'){
                    this.product.price = currency
                }else if(input == 'total_price'){
                    this.product.total_price = currency
                }
            }
        },
        // ============= PRICE_FORMATTER END =============










        // ============= CLEARS_AND_CLOSES START =============
        clearInputs(){
            this.product = {
                from_where: '',
                amount: '',
                price: '',
                total_price: '',
            }
            this.date = ''
            this.clearErrors()
        },
        clearErrors(){
            this.errorStatus = false;
            this.errorMessage = '';
            this.errorPlace = '';
        },
        closeEditDialog(){
            this.clearInputs();
            this.editModal = false;
            this.$refs.formEditIncomeAcessory.reset();
        },
        // ============= CLEARS_AND_CLOSES END =============















        // ============= ON_SUBMIT START =============
        async onSubmit(){
            this.$refs.formEditIncomeAcessory.validate().then(async(success) => {
                if(success){
                    // ============= IF SUCCESS =============


                    // ============= EDITING_IP START =============
                    let ip = await this.updateProductIncome(
                        {
                            id: this.product.id,
                            from_where: this.product.from_where,
                            amount: parseFloat(this.product.amount.toString().replace(/,/g, '')),
                            price: parseFloat(this.product.price.toString().replace(/,/g, '')),
                            total_price: parseFloat(this.product.total_price.toString().replace(/,/g, '')),
                            type: 'accessory'
                        })

                    if(ip.response && ip.response.status == 422){
                        if(ip.response.data.errors.from_where){
                            this.errorMessage = ip.response.data.errors.from_where[0];
                            this.errorPlace = 'from_where'
                        }else if(ip.response.data.errors.amount){
                            this.errorMessage = ip.response.data.errors.amount[0];
                            this.errorPlace = 'amount'
                        }else if(ip.response.data.errors.price){
                            this.errorMessage = ip.response.data.errors.price[0];
                            this.errorPlace = 'price'
                        }else if(ip.response.data.errors.total_price){
                            this.errorMessage = ip.response.data.errors.total_price[0];
                            this.errorPlace = 'total_price'
                        }
                        this.errorStatus = true
                        this.$toast.error(this.errorMessage);
                    }else if(ip.response && ip.response.status == 409){
                        if(ip.response.data.message){
                            this.errorMessage = ip.response.data.message;
                        }
                        this.$toast.error(this.errorMessage);
                    }else{
                        let toastTitle = 'Muvaffaqiyatli tahrirlandi!'
                        this.$toast.success(toastTitle);
                        this.closeEditDialog();
                        this.refresh()
                        // this.$store.commit('main/SET_UPDATE_MAVJUD_IPS', true)
                    }
                    // ============= EDITING_IP END =============
                }else{
                    // ============= IF NO SUCCESS =============
                    this.$toast.error('Xato yuz berdi!');
                }
            })
        }
        // ============= ON_SUBMIT END =============



    }
}
</script>

<style>

</style>
